import { Component, OnInit } from "@angular/core";
import { DiscoverCitiesService } from "./discover-cities.service";
import { environment } from "src/environments/environment";
import { ToastrService } from "ngx-toastr";
import * as _ from "lodash";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { Observable } from "rxjs";

@Component({
	selector: "app-discover-cities",
	templateUrl: "./discover-cities.component.html",
	styleUrls: ["./discover-cities.component.css"],
})
export class DiscoverCitiesComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;

	isAdmin: boolean = false;
	selectedAlphabet: any;
	server_url = environment.server_url;
	allCities: any[] = [];
	alphabetFilterItems: any;
	loading: boolean;
	autoSuggestions: any;
	filterData: any = {
		filter: {
			status: "active",
		},
		search: {
			name: {
				contains: "",
				startsWith: "",
			},
		},
		sort: [
			{
				name: "DESC",
			},
		],
		pg: {
			skip: 0,
			limit: 200,
		},
	};
	constructor(private _dss: DiscoverCitiesService, private toastr: ToastrService) {
		this.loggedInUser$.subscribe((resp) => {
			if (resp.LoggedInUser && resp.LoggedInUser.email == "admin@forge.com") {
				this.isAdmin = true;
			}
		});
		let asciCode = _.range("A".charCodeAt(0), "Z".charCodeAt(0) + 1);
		this.alphabetFilterItems = asciCode.map((i) => String.fromCharCode(i));
	}

	ngOnInit() {
		this.selectedAlphabet = "All";
		this.getAllCities();
	}

	async getAllCities() {
		try {
			this.loading = true;
			this.allCities = await this._dss.getAllCities(this.filterData);
			this.loading = false;
		} catch (err) {
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	filterByAlphabet(startsWith) {
		this.selectedAlphabet = startsWith;
		if (startsWith == "All") {
			this.filterData.search.name.startsWith = "";
		} else {
			this.filterData.search.name.startsWith = startsWith;
		}
		this.getAllCities();
	}

	searchCities() {
		this.autoSuggestions = null;
		this.getAllCities();
	}

	searchAutoSuggestion(keyword) {
		this.filterData.search.name.contains = keyword;
		this.searchCities();
	}

	async autoComplete(keyword) {
		if (keyword.length > 3) {
			this.autoSuggestions = await this._dss.getSearchSuggestions({
				search: {
					name: { contains: keyword },
				},
			});
		} else {
			this.autoSuggestions = null;
		}
	}
}
