import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { Validators, FormGroup, FormBuilder } from "@angular/forms";
import { ToastrService } from "ngx-toastr";
import { Router } from "@angular/router";
import { AddSolutionWidgetService } from "./add-solution-widget.service";
import * as _ from "lodash";

@Component({
	selector: "app-add-solutions",
	templateUrl: "./add-solutions.component.html",
	styleUrls: ["./add-solutions.component.css"],
})
export class AddSolutionsComponent implements OnInit {
	@Input() solutionsImplemented: any;
	@Output() onSolutionChange = new EventEmitter<any>();
	challengeSector: any;
	challengeBasedOnSector: any;
	loading: boolean;
	isChallengeLoaded: boolean;
	addSolutionForm: FormGroup;
	isSubmitted: boolean;
	solutionsArr: any = [];
	urlPattern: any = new RegExp("^(http|https)://");
	constructor(private toastr: ToastrService, private router: Router, private _ws: AddSolutionWidgetService, private fb: FormBuilder) {
		this.addSolutionForm = this.fb.group({
			challengeSector: ["", [Validators.required]],
			challenge: ["", [Validators.required]],
			link: ["", [Validators.required, Validators.pattern(this.urlPattern)]],
			description: ["", [Validators.maxLength(800)]],
		});
	}

	async ngOnInit() {
		await this.fetchChallengeSectors();
		if (this.solutionsImplemented) {
			this.solutionsArr = this.solutionsImplemented;
			this.onSolutionChange.emit(this.solutionsArr);
		}
	}

	get addSolutionFormControl() {
		return this.addSolutionForm.controls;
	}

	async fetchChallengeSectors() {
		try {
			this.loading = true;
			this.challengeSector = await this._ws.getChallengeSectors();
			this.loading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	getChallengeBasedOnSector(event) {
		let challengeName = event.target.value;
		if (challengeName) {
			let sectors = this.challengeSector.data;
			let challenges = _.find(sectors, (sector) => {
				if (sector.name == challengeName) return sector.challenges;
			});
			this.challengeBasedOnSector = challenges.challenges;
			this.isChallengeLoaded = true;
		} else {
			this.challengeBasedOnSector = null;
		}
	}

	insertSolutions() {
		this.isSubmitted = true;
		if (this.addSolutionForm.valid) {
			this.solutionsArr.push(this.addSolutionForm.value);
			this.isSubmitted = false;
			this.isChallengeLoaded = false;
			this.addSolutionForm.reset();
			this.onSolutionChange.emit(this.solutionsArr);
			document.getElementById("addSolutions").click();
		} else {
			this.toastr.error("Please provide the required values to proceed further", "Validation Error");
		}
	}

	handleItemEdit(editedData) {
		let editIndex = editedData.index;
		let editArr = this.solutionsArr;
		if (~editIndex) {
			editArr[editIndex] = editedData.data;
			this.solutionsArr = editArr;
			this.onSolutionChange.emit(this.solutionsArr);
		}
	}

	handleItemDelete(index) {
		if (~index) {
			this.solutionsArr.splice(index, 1);
			this.onSolutionChange.emit(this.solutionsArr);
		}
	}
}
