import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { ImageCroppedEvent, base64ToFile } from "ngx-image-cropper";
import { environment } from "../../../../environments/environment";
@Component({
	selector: "app-city-map-upload",
	templateUrl: "./city-map-upload.component.html",
	styleUrls: ["./city-map-upload.component.css"],
})
export class CityMapUploadComponent implements OnInit {
	@Input() cityMap: any;
	@Input() Src: string;
	@Output() onSaveMap = new EventEmitter<File>();
	@Output() onRemoveMap = new EventEmitter<File | string>();
	// map
	mapChangedEvent: any = "";
	croppedMap: any = "";
	mapLoaded: boolean = false;
	mapSaved: boolean = false;
	serverUrl: string = environment.server_url;
	constructor() {}

	ngOnInit() {
		if (this.cityMap) {
			this.Src = this.serverUrl + this.cityMap;
		}
	}

	mapChangeEvent(event: any): void {
		this.mapChangedEvent = event;
	}

	mapCropped(event: ImageCroppedEvent) {
		this.croppedMap = event.base64;
	}

	mapLoadedEvent() {
		this.mapLoaded = true;
	}

	saveCropedMap() {
		const fileBeforeCrop = this.mapChangedEvent.target.files[0];
		this.Src = this.croppedMap;
		this.onSaveMap.emit(
			new File([base64ToFile(this.croppedMap)], fileBeforeCrop.name, {
				type: fileBeforeCrop.type,
			})
		);
		this.mapSaved = true;
		document.getElementById("mapModal").click();
	}

	removeImage() {
		if (isBase64(this.Src)) {
			const fileBeforeCrop = this.mapChangedEvent.target.files[0];
			this.onRemoveMap.emit(
				new File([base64ToFile(this.croppedMap)], fileBeforeCrop.name, {
					type: fileBeforeCrop.type,
				})
			);
		} else {
			this.onRemoveMap.emit(this.Src);
		}
		this.mapChangedEvent = null;
		this.croppedMap = "";
		this.mapLoaded = false;
		this.Src = null;
	}
}

function isBase64(str) {
	if (str === "" || str.trim() === "") {
		return false;
	}
	try {
		base64ToFile(str);
		return true;
	} catch (err) {
		return false;
	}
}
