import { Injectable } from "@angular/core";
import { environment } from "src/environments/environment";
import { HttpClient } from "@angular/common/http";

@Injectable({
	providedIn: "root",
})
export class AddCityServicesService {
	api_url = environment.api_url;
	constructor(private http: HttpClient) {}

	async createCity(postData) {
		return await this.http.post(this.api_url + "/smart_cities/create", postData).toPromise();
	}

	async uploadAvatar(postData) {
		return await this.http.post(this.api_url + "/smart_cities/upload", postData).toPromise();
	}

	async uploadCeoAvatar(postData) {
		return await this.http.post(this.api_url + "/smart_cities/upload_ceo_avatar", postData).toPromise();
	}

	async uploadLogo(postData) {
		return await this.http.post(this.api_url + "/smart_cities/upload_logo", postData).toPromise();
	}

	async uploadMap(postData) {
		return await this.http.post(this.api_url + "/smart_cities/city_map", postData).toPromise();
	}

	async uploadGallery(postData) {
		return await this.http.post(this.api_url + "/smart_cities/upload_gallery", postData).toPromise();
	}

	async getChallengeSectors() {
		return await this.http
			.post(this.api_url + "/challenge_sectors/select_mul", {
				pg: {
					offset: 0,
					limit: 200,
				},
			})
			.toPromise();
	}

	async getChallenges(sectorId) {
		return await this.http
			.post(this.api_url + "/challenges/select_mul", {
				filter: {
					frg_challenge_sector_id: sectorId,
				},
				pg: {
					offset: 0,
					limit: 400,
				},
			})
			.toPromise();
	}

	async addChallenges(data) {
		return await this.http.post(this.api_url + "/challenges_and_smart_cities/create", data).toPromise();
	}

	async addChallengeSectors(data) {
		return await this.http.post(this.api_url + "/smart_cities_and_challenge_sectors/create", data).toPromise();
	}
}
