import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { IDropdownSettings } from "ng-multiselect-dropdown";
import { Validators, FormGroup, FormBuilder } from "@angular/forms";

import { ToastrService } from "ngx-toastr";
import * as _ from "lodash";
import { EditCityServicesService } from "./edit-city-services.service";
import { environment } from "../../../environments/environment";
import { LoggedInUser } from "src/app/state/model/loggedinuser.model";
import { LoggedInUserState, LoggedInUserStateModel } from "src/app/state/loggedinuser.state";
import { Observable } from "rxjs";
import { Select } from "@ngxs/store";

@Component({
	selector: "app-edit-city",
	templateUrl: "./edit-city.component.html",
	styleUrls: ["./edit-city.component.css"],
})
export class EditCityComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
	initLoading: boolean;
	cityData: any;
	serverUrl = environment.server_url;
	loading: boolean = false;
	cityStep1: boolean;
	cityStep2: boolean;
	addCityStep1: FormGroup;
	addCityStep2: FormGroup;
	isSubmitted: any = {
		Step1: false,
		Step2: false,
	};
	avatar: File;
	logo: File;
	CEOAvatar: File;
	map: File;
	cityGallery: any = [];
	private currentCityGallery: any;
	challengeSector: any;
	challengeBasedOnSector: any;
	sectorDropdownList = [];
	dropdownSettings: IDropdownSettings;
	selectedSectors = [];
	challengeDropdownList = [];
	disableChallengeList: boolean = true;
	solutionsImplemented = [];
	cityAchievements = [];
	implemented_solutions: any;
	selectedChallengeSectors = [];
	selectedChallenges = [];
	tobeRemovedFilePaths = [];
	urlPattern: any = new RegExp("^(http|https)://");
	id: any;

	constructor(
		private route: ActivatedRoute,
		private fb: FormBuilder,
		private _city: EditCityServicesService,
		private toastr: ToastrService,
		private router: Router
	) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});
	}

	async ngOnInit() {
		this.route.params.subscribe((params) => (this.id = params.id));
		this.initLoading = true;
		this.cityStep1 = false;
		this.cityStep2 = true;
		await this.getChallengeSector();
		this.addCityStep1 = this.fb.group({
			ceoName: ["", [Validators.required]],
			email: ["", [Validators.required, Validators.email]],
			cityName: ["", [Validators.required]],
			vision: ["", [Validators.required, Validators.maxLength(500)]],
			challengeSectors: [[], [Validators.required]],
			challenges: [[], [Validators.required]],
			website: ["", [Validators.required, Validators.pattern(this.urlPattern)]],
			mobile: ["", [Validators.pattern("(0|91)?[5-9][0-9]{9}")]],
			landLineCode: [""],
			landLineNumber: [""],
			area: ["", [Validators.required]],
			population: ["", [Validators.required]],
			budget: ["", [Validators.required]],
			rank: ["", [Validators.required]],
		});
		this.addCityStep2 = this.fb.group({
			dataSheet: ["", [Validators.pattern(this.urlPattern)]],
			ig: ["", [Validators.pattern(this.urlPattern)]],
			yt: ["", [Validators.pattern(this.urlPattern)]],
			fb: ["", [Validators.pattern(this.urlPattern)]],
			twtr: ["", [Validators.pattern(this.urlPattern)]],
		});
		await this.getCityWithId();
		this.initLoading = false;
	}

	async getCityWithId() {
		try {
			this.cityData = await this._city.getCityWithId(this.id);
			this.cityData.data.challenge_sectors.forEach((el) => {
				this.selectedChallengeSectors.push({
					item_id: parseInt(el.id),
					item_text: el.name,
				});
			});
			this.cityData.data.challenges.forEach((el) => {
				this.selectedChallenges.push({
					item_id: parseInt(el.id),
					item_text: el.name,
				});
			});
			let phnum;
			if (/^\d+$/.test(this.cityData.data.mobile[0].mobile.split("-")[1])) {
				phnum = this.cityData.data.mobile[0].mobile.split("-")[1];
			} else {
				phnum = null;
			}
			let lcode;
			let lnum;
			if (
				/^\d+$/.test(this.cityData.data.mobile[0].landline.split("-")[1]) &&
				/^\d+$/.test(this.cityData.data.mobile[0].landline.split("-")[1])
			) {
				lcode = this.cityData.data.mobile[0].landline.split("-")[0];
				lnum = this.cityData.data.mobile[0].landline.split("-")[1];
			} else {
				lcode = null;
				lnum = null;
			}
			//  populate challenges in dropdown according to selected sectors
			if (this.cityData.data) {
				this.addCityStep1.patchValue({
					ceoName: this.cityData.data.ceo_name,
					email: this.cityData.data.ceo_email,
					cityName: this.cityData.data.name,
					vision: this.cityData.data.vision,
					challengeSectors: this.selectedChallengeSectors,
					challenges: this.selectedChallenges,
					website: this.cityData.data.website,
					mobile: phnum,
					landLineCode: lcode,
					landLineNumber: lnum,
					area: this.cityData.data.city_area,
					population: this.cityData.data.city_demographics,
					budget: this.cityData.data.budget,
					rank: this.cityData.data.score,
				});
				this.addCityStep2 = this.fb.group({
					dataSheet: this.cityData.data.data_sheet,
					ig: this.cityData.data.social_media[1].instagram,
					yt: this.cityData.data.social_media[3].youtube,
					fb: this.cityData.data.social_media[0].facebook,
					twtr: this.cityData.data.social_media[2].twitter,
				});
				this.implemented_solutions = this.cityData.data.implemented_solutions;
				if (this.cityData.data.ceo_email != this.loggedInUser.email) {
					this.router.navigateByUrl("cities");
					this.toastr.warning("", "Only City Admin can edit City Info");
				}
			}
			await this.initChallengesDropdown();
			this.currentCityGallery = _.cloneDeep(this.cityData.data.gallery);
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	get addCityControlStep1() {
		return this.addCityStep1.controls;
	}

	get addCityControlStep2() {
		return this.addCityStep2.controls;
	}

	gotoStep(step) {
		switch (step) {
			case "step1":
				this.cityStep1 = false;
				this.cityStep2 = true;
				window.scroll(0, 0);
				break;
			case "step2":
				this.cityStep1 = true;
				this.cityStep2 = false;
				window.scroll(0, 0);
				break;
		}
	}

	CityStep1Submit() {
		this.isSubmitted.Step1 = true;
		if (this.addCityStep1.valid) {
			this.gotoStep("step2");
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	async updateSmartCity() {
		if (this.addCityStep1.valid && this.addCityStep2.valid) {
			this.loading = true;
			let cityPostData = {
				name: this.addCityStep1.value.cityName,
				ceo_name: this.addCityStep1.value.ceoName,
				ceo_email: this.addCityStep1.value.email,
				vision: this.addCityStep1.value.vision,
				score: this.addCityStep1.value.rank,
				budget: this.addCityStep1.value.budget,
				city_area: this.addCityStep1.value.area,
				city_demographics: this.addCityStep1.value.population,
				website: this.addCityStep1.value.website,
				mobile: [
					{
						mobile: "+91-" + this.addCityStep1.value.mobile,
						landline:
							this.addCityStep1.value.landLineNumber == ""
								? ""
								: `${this.addCityStep1.value.landLineCode}-${this.addCityStep1.value.landLineNumber}`,
					},
				],
				data_sheet: this.addCityStep2.value.dataSheet,
				implemented_solutions: this.solutionsImplemented,
				achievements: this.cityAchievements,
				social_media: [
					{ facebook: this.addCityStep2.value.fb },
					{ instagram: this.addCityStep2.value.ig },
					{ twitter: this.addCityStep2.value.twtr },
					{ youtube: this.addCityStep2.value.yt },
				],
			};
			try {
				let updatedSmartCity: any = await this._city.updateCity({
					slug: { id: this.id },
					data: cityPostData,
				});
				await Promise.all([
					this.saveAvatar(updatedSmartCity.data.id),
					this.saveLogo(updatedSmartCity.data.id),
					this.saveMap(updatedSmartCity.data.id),
					this.saveCEOAvatar(updatedSmartCity.data.id),
					await this.associateChallengeSectors(updatedSmartCity.data.id),
					await this.associateChallenges(updatedSmartCity.data.id),
					await this.filesRemovedFromGallery(updatedSmartCity.data.id),
					await this.saveGallery(updatedSmartCity.data.id),
				]);
				this.loading = false;
				this.toastr.success("", "Smart City updated successfully");
				this.router.navigate([`/cities/${updatedSmartCity.data.id}`]);
			} catch (err) {
				console.log(err);
				this.toastr.error("", "Something went wrong please try again");
			}
		} else {
			this.toastr.error("please provide the required values to proceed further.", "Validation Error");
		}
	}

	async getChallengeSector() {
		try {
			this.challengeSector = await this._city.getChallengeSectors();
			this.challengeSector.data.forEach((element) => {
				this.sectorDropdownList.push({
					item_id: parseInt(element.id),
					item_text: element.name,
				});
			});
			this.dropdownSettings = {
				singleSelection: false,
				idField: "item_id",
				textField: "item_text",
				enableCheckAll: false,
				allowSearchFilter: true,
			};
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async getChallengeBasedOnSector() {
		// manually setting the touched event since plugin doesnt work properly
		this.addCityStep1.controls.challengeSectors.markAsTouched();
		this.selectedChallenges = this.addCityStep1.value.challenges;
		let idArr = [];
		this.challengeDropdownList = [];
		this.addCityStep1.value.challengeSectors.forEach((element) => {
			idArr.push(element.item_id);
		});
		try {
			this.toastr.info("Fetching challenges based on sectors", "Info");
			this.challengeBasedOnSector = await this._city.getChallenges(idArr);
			this.challengeBasedOnSector.data.forEach((element) => {
				this.challengeDropdownList = _.unionBy(
					this.challengeDropdownList,
					[{ item_id: parseInt(element.id), item_text: element.name }],
					"item_id"
				);
			});
			this.selectedChallenges = _.intersectionBy(this.selectedChallenges, this.challengeDropdownList, "item_id");
			if (_.isArray(this.selectedChallenges)) {
				this.addCityStep1.patchValue({
					challenges: this.selectedChallenges,
				});
			} else {
				this.addCityStep1.patchValue({
					challenges: [this.selectedChallenges],
				});
			}
			// manually setting the touched event since plugin doesnt work properly
			this.addCityStep1.controls.challenges.markAsTouched();
			// console.log("selected values should be", this.addCityStep1.value.challenges);
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	handleChallengeChange(event) {
		// manually setting the touched event since plugin doesnt work properly
		this.addCityStep1.controls.challenges.markAsTouched();
	}

	async initChallengesDropdown() {
		this.disableChallengeList = true;
		let idArr = [];
		this.challengeDropdownList = [];
		// console.log(this.addCityStep1.value.challengeSectors);
		this.addCityStep1.value.challengeSectors.forEach((element) => {
			idArr.push(element.item_id);
		});
		try {
			this.challengeBasedOnSector = await this._city.getChallenges(idArr);
			this.challengeBasedOnSector.data.forEach((element) => {
				this.challengeDropdownList.push({ item_id: parseInt(element.id), item_text: element.name });
			});
			this.disableChallengeList = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async associateChallenges(cityId) {
		if (this.addCityStep1.controls.challenges.touched) {
			// removing all challenges which are previously set.
			let filterPreviousAssociationsByCityId: any = await this._city.getSmartCityChallenge({
				filter: { frg_smart_city_id: cityId },
			});
			let idsToBeDeleted = filterPreviousAssociationsByCityId.data.map((e) => e.id);
			await this._city.deleteCityChallenge({
				slug: {
					ids: idsToBeDeleted,
				},
			});
			// newly creating the association
			if (this.addCityStep1.value.challenges.length > 0) {
				this.addCityStep1.value.challenges.forEach(async (element) => {
					await this._city.addChallenges({
						data: {
							frg_challenge_id: element.item_id,
							frg_smart_city_id: cityId,
						},
					});
				});
			}
		}
	}

	async associateChallengeSectors(cityId) {
		if (this.addCityStep1.controls.challengeSectors.touched) {
			// removing all sectors which are previously set.
			let filterPreviousAssociationsByCityId: any = await this._city.getFilteredChallengeSectorSmartCities({
				filter: {
					frg_smart_city_id: cityId,
				},
			});
			let idsToBeDeleted = filterPreviousAssociationsByCityId.data.map((e) => e.id);
			await this._city.deleteExisitingSmartCityChallengeSectors({
				slug: { ids: idsToBeDeleted },
			});
			// newly creating the association
			if (this.addCityStep1.value.challengeSectors.length > 0) {
				this.addCityStep1.value.challengeSectors.forEach(async (element) => {
					await this._city.addChallengeSectors({
						data: {
							frg_challenge_sector_id: element.item_id,
							frg_smart_city_id: cityId,
						},
					});
				});
			}
		}
	}

	async filesRemovedFromGallery(id) {
		if (this.tobeRemovedFilePaths.length > 0) {
			await this._city.removeFromGallery({
				slug: { id: id },
				data: { remove_paths: this.tobeRemovedFilePaths },
			});
		}
	}

	async saveAvatar(id) {
		if (this.avatar) {
			let formData = new FormData();
			formData.append("slug", id);
			formData.append("avatar", this.avatar, this.avatar.name);
			await this._city.uploadAvatar(formData);
		}
	}

	async saveCEOAvatar(id) {
		if (this.CEOAvatar) {
			let formData = new FormData();
			formData.append("slug", id);
			formData.append("ceo_avatar", this.CEOAvatar, this.CEOAvatar.name);
			await this._city.uploadCeoAvatar(formData);
		}
	}

	async saveLogo(id) {
		if (this.logo) {
			let formData = new FormData();
			formData.append("slug", id);
			formData.append("city_logo", this.logo, this.logo.name);
			await this._city.uploadLogo(formData);
		}
	}

	async saveMap(id) {
		if (this.map) {
			let formData = new FormData();
			formData.append("slug", id);
			formData.append("city_map", this.map, this.map.name);
			await this._city.uploadMap(formData);
		}
	}

	async saveGallery(id) {
		if (this.cityGallery) {
			let formData = new FormData();
			formData.append("slug", id);
			this.cityGallery.forEach((file) => {
				formData.append("gallery", file);
			});
			await this._city.uploadGallery(formData);
		}
	}

	handleCEOAvatar(file) {
		this.CEOAvatar = file;
	}

	handleSaveLogo(file) {
		this.logo = file;
	}

	handleSaveCityImage(file) {
		this.avatar = file;
	}

	handleSaveMap(file) {
		this.map = file;
	}

	handleSaveGallery(files) {
		let currentGalleryBeforeChange = this.cityData.data.gallery;
		// splitting the array with
		let newFiles: File = files.filter((e) => {
			if (typeof e == "object") {
				return e;
			}
		});
		let updatedFilePaths: string = files.filter((e) => {
			if (typeof e == "string") {
				return e;
			}
		});
		this.tobeRemovedFilePaths = currentGalleryBeforeChange.filter((x) => !updatedFilePaths.includes(x));
		this.cityGallery = newFiles;
	}

	handleRemovedLogo(file) {
		if (typeof file == "object") this.logo = null;
	}

	handleRemoveCityImage(file) {
		if (typeof file == "object") this.avatar = null;
	}

	handleRemoveMap(file) {
		if (typeof file == "object") this.map = null;
	}

	handleSolutionChanges(solutionData) {
		this.solutionsImplemented = solutionData;
	}

	handleAchievementChanges(achievementData) {
		this.cityAchievements = achievementData;
	}
}

