import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

import { CitiesComponent } from "./cities.component";
import { AddCityComponent } from "./add-city/add-city.component";
import { CityProfileComponent } from "./city-profile/city-profile.component";
import { DiscoverCitiesComponent } from "./discover-cities/discover-cities.component";
import { EditCityComponent } from "./edit-city/edit-city.component";

const routes: Routes = [
	{
		path: "",
		component: CitiesComponent,
		children: [
			{
				path: "",
				component: DiscoverCitiesComponent,
				pathMatch: "full",
			},
			{
				path: "create",
				component: AddCityComponent,
			},
			{
				path: ":id",
				component: CityProfileComponent,
			},
			{
				path: ":id/edit",
				component: EditCityComponent,
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class CitiesRoutingModule {}
