import { Component, OnInit, OnChanges } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { CityProfileService } from "./city-profile.service";
import { environment } from "../../../environments/environment";
import { ToastrService } from "ngx-toastr";
import * as _ from "lodash";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

@Component({
	selector: "app-city-profile",
	templateUrl: "./city-profile.component.html",
	styleUrls: ["./city-profile.component.css"],
})
export class CityProfileComponent implements OnInit, OnChanges {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	id: any;
	cityData: any = {};
	cityChallenges: any = {};
	challengesLoading: boolean;
	server_url: any = environment.server_url;
	cityLoading: boolean;
	mobile: any;
	landline: any;
	isCityAdmin: boolean;

	constructor(
		private route: ActivatedRoute,
		private _cps: CityProfileService,
		private toastr: ToastrService,
		private router: Router
	) {
		this.id = this.route.snapshot.params.id;
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});
	}

	ngOnInit() {
		this.getCityData();
	}

	ngOnChanges() { }

	async getCityData() {
		try {
			this.cityLoading = true;
			this.challengesLoading = true;
			this.cityData = await this._cps.getCityInformation({
				slug: { id: this.id },
			});

			if (this.cityData.data.seekers) {
				this.cityData.data.seekers.forEach((element) => {
					if (element.id == this.loggedInUser.userTypeId) {
						this.isCityAdmin = true;
					}
				});
			}
			if (this.cityData.data.mobile[0].mobile.split("-")[1]) {
				this.mobile = this.cityData.data.mobile[0].mobile;
			} else {
				this.mobile = null;
			}
			if (
				/^\d+$/.test(this.cityData.data.mobile[0].landline.split("-")[1]) &&
				/^\d+$/.test(this.cityData.data.mobile[0].landline.split("-")[1])
			) {
				this.landline = this.cityData.data.mobile[0].landline;
			} else {
				this.landline = null;
			}
			this.cityLoading = false;
			let challengesResponse: any = await this._cps.getCityChallenges({
				filter: { frg_smart_city_id: this.id },
			});
			this.cityChallenges = challengesResponse.data;
			this.challengesLoading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}
	editCity() {
		if (this.cityData.data.ceo_email == this.loggedInUser.email) {
			this.router.navigateByUrl(`cities/${this.cityData.data.id}/edit`);
		} else {
			this.toastr.warning("", "City Admin can edit City Info");
		}
	}
}
