import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { ImageCroppedEvent, base64ToFile } from "ngx-image-cropper";
import { environment } from "../../../../environments/environment";

@Component({
	selector: "app-city-avatar-upload",
	templateUrl: "./city-avatar-upload.component.html",
	styleUrls: ["./city-avatar-upload.component.css"],
})
export class CityAvatarUploadComponent implements OnInit {
	@Input() Src: string;
	@Output() onSaveCityImage = new EventEmitter<File>();
	@Output() onRemoveCityImage = new EventEmitter<File | string>();

	cityavatarChangedEvent: any = "";
	croppedCityAvatar: any = "";
	cityavatarLoaded: boolean = false;
	cityavatarSaved: boolean = false;
	serverUrl: string = environment.server_url;
	constructor() {}

	ngOnInit() {
		// console.log(this.serverUrl);
	}

	cityavatarChangeEvent(event: any): void {
		this.cityavatarChangedEvent = event;
	}
	cityavatarCropped(event: ImageCroppedEvent) {
		this.croppedCityAvatar = event.base64;
	}
	cityavatarLoadedEvent() {
		this.cityavatarLoaded = true;
	}

	saveCropedCityAvatar() {
		const fileBeforeCrop = this.cityavatarChangedEvent.target.files[0];
		this.Src = this.croppedCityAvatar;

		this.onSaveCityImage.emit(
			new File([base64ToFile(this.croppedCityAvatar)], fileBeforeCrop.name, {
				type: fileBeforeCrop.type,
			})
		);
		this.cityavatarSaved = true;
		document.getElementById("cityavatarModal").click();
	}

	removeImage() {
		if (isBase64(this.Src)) {
			const fileBeforeCrop = this.cityavatarChangedEvent.target.files[0];
			this.onRemoveCityImage.emit(
				new File([base64ToFile(this.croppedCityAvatar)], fileBeforeCrop.name, {
					type: fileBeforeCrop.type,
				})
			);
		} else {
			this.onRemoveCityImage.emit(this.Src);
		}
		this.cityavatarChangedEvent = null;
		this.croppedCityAvatar = "";
		this.Src = null;
		this.cityavatarLoaded = false;
	}
}

function isBase64(str) {
	if (str === "" || str.trim() === "") {
		return false;
	}
	try {
		base64ToFile(str);
		return true;
	} catch (err) {
		return false;
	}
}
