import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "src/environments/environment";

@Injectable({
	providedIn: "root",
})
export class CityProfileService {
	api_url = environment.api_url;

	constructor(private http: HttpClient) {}

	async getCityInformation(cityId) {
		return await this.http.post(this.api_url + "/smart_cities/select", cityId).toPromise();
	}

	async getCityChallenges(cityId) {
		let response: any = await this.http.post(this.api_url + "/challenges_and_smart_cities/select_mul", cityId).toPromise();
		let challengeIds = [];
		response.data.forEach((challenge) => {
			challengeIds.push(challenge.frg_challenge_id);
		});
		return await this.http
			.post(this.api_url + "/challenges/select_mul", {
				filter: { id: challengeIds },
				pg: {
					skip: 0,
					limit: 100,
				},
			})
			.toPromise();
	}
}
